:root {
    box-sizing: border-box;
    --main-font: "Open Sans", "Helvetica", arial;
    --main-bg-color: #fff;
    --main-color: #20b2aa;
}

*,
::before,
::after {
    box-sizing: border-box;
}

html,
body {
    min-height: 100vh;
}

h1,
h2,
h3,
h4,
h5,
.entry-title {
    margin: 0 0 1.612rem;
    line-height: 1.2;
    font-family: "Roboto Condensed";
    font-weight: 700;
    text-transform: uppercase;
}

a:link {
    color: #085551;
    text-decoration: none;
}

a:visited {
    color: #085551;
}

a:hover {
    text-decoration: underline;
}

a:active {
    color: #ff6358;
}

body {
    font-family: var(--main-font);
    margin: 0;
    max-width: 1280px;
    margin: 0 auto;
    font-size: calc(0.5em + 1vw);
    line-height: 1.6;
}

.grid {
    display: grid;
    grid-gap: 10px;
    grid-template-columns: 1fr;
    grid-template-rows: 50px 1fr 70px;

    grid-template-areas:
        "header"
        "main"
        "footer";
}

header {
    grid-area: header;
    background-color: var(--main-bg-color);
    position: sticky;
    top: 0;
    z-index: 9;
}

.site-header__logo {
    color: var(--main-color);
    font-family: Lobster;
    font-size: 2rem;
}

main {
    grid-area: main;
    padding: 0 20px;
    overflow: hidden;
}

footer {
    grid-area: footer;
    background-color: var(--main-bg-color);
    padding: 0px 20px;
}

.site-header>.wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    padding: 0px 20px;
}

.main-content {
    max-width: 900px;
    margin: 0 auto;
}

.splide {
    padding: 1em 3em !important;
}

.splide__pagination {
    margin-bottom: -20px !important;
}

footer {
    background-color: gold;
    background-image: linear-gradient(yellow, gold);
}

footer>.wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}

.footer__full-name {
    margin-right: auto;
    color: #085551;
}

.wrap>a {
    background-color: #fff;
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    border-radius: 50%;
    margin-left: 20px;
}

.footer__icon::before {
    color: black;
}

.k-menu {
    font-size: 1rem;
}